// frontend/src/app/layout.tsx
import type { Metadata } from 'next';
import { Inter, Manrope } from 'next/font/google';
import './globals.css';
import Header from '@/components/layout/Header';
import ThemeScript from '@/components/ThemeScript';

const inter = Inter({
  subsets: ['latin'],
  variable: '--font-inter',
  display: 'swap',
  preload: true,
});

const manrope = Manrope({
  subsets: ['latin'],
  variable: '--font-manrope',
  display: 'swap',
  preload: true,
});

export const metadata: Metadata = {
  title: 'VIBECRO - Solutions Digitales Innovantes',
  description: 'IoT, IA & D√©veloppement sur-mesure pour transformer votre entreprise',
  keywords: ['IoT', 'IA', 'd√©veloppement web', 'solutions digitales', 'VIBECRO'],
  authors: [{ name: 'VIBECRO' }],
  robots: {
    index: true,
    follow: true,
    googleBot: {
      index: true,
      follow: true,
      'max-video-preview': -1,
      'max-image-preview': 'large',
      'max-snippet': -1,
    },
  },
  openGraph: {
    type: 'website',
    locale: 'fr_FR',
    url: 'https://vibecro.com',
    title: 'VIBECRO - Solutions Digitales Innovantes',
    description: 'IoT, IA & D√©veloppement sur-mesure pour transformer votre entreprise',
    siteName: 'VIBECRO',
    images: [
      {
        url: '/images/og-image.jpg',
        width: 1200,
        height: 630,
        alt: 'VIBECRO Solutions Digitales',
      },
    ],
  },
  twitter: {
    card: 'summary_large_image',
    title: 'VIBECRO - Solutions Digitales Innovantes',
    description: 'IoT, IA & D√©veloppement sur-mesure pour transformer votre entreprise',
    images: ['/images/twitter-image.jpg'],
    creator: '@vibecro',
  },
  viewport: {
    width: 'device-width',
    initialScale: 1,
    maximumScale: 1,
  },
};

export default function RootLayout({
  children,
}: {
  children: React.ReactNode;
}) {
  return (
    <html lang="fr" className={`${inter.variable} ${manrope.variable}`} suppressHydrationWarning>
      <head>
        {/* Preconnect to domains */}
        <link rel="preconnect" href="https://fonts.googleapis.com" />
        <link rel="preconnect" href="https://fonts.gstatic.com" crossOrigin="anonymous" />
        
        {/* Preload critical resources */}
        <link rel="preload" href="/fonts/custom.woff2" as="font" type="font/woff2" crossOrigin="anonymous" />
        
        {/* Preload page links */}
        <link rel="preload" href="/services" as="document" />
        <link rel="preload" href="/portfolio" as="document" />
        <link rel="preload" href="/contact" as="document" />
        
        {/* Script CRITIQUE pour appliquer le th√®me AVANT le rendu */}
        <script
          dangerouslySetInnerHTML={{
            __html: `
              (function() {
                // 1. V√©rifier le th√®me sauvegard√©
                var savedTheme = localStorage.getItem('vibecro-theme');
                
                // 2. V√©rifier la pr√©f√©rence syst√®me
                var systemPrefersDark = window.matchMedia('(prefers-color-scheme: dark)').matches;
                
                // 3. D√©terminer le th√®me
                var theme = savedTheme;
                if (!theme) {
                  theme = systemPrefersDark ? 'dark' : 'light';
                }
                
                // 4. Appliquer IMM√âDIATEMENT (tr√®s important !)
                if (theme === 'dark') {
                  document.documentElement.classList.add('dark');
                  document.documentElement.style.backgroundColor = '#0a0a0a';
                } else {
                  document.documentElement.classList.remove('dark');
                  document.documentElement.style.backgroundColor = '#ffffff';
                }
                
                // 5. Sauvegarder pour coh√©rence
                localStorage.setItem('vibecro-theme', theme);
                
                console.log('üéØ Th√®me initialis√©:', theme);
              })();
            `,
          }}
        />
      </head>
      <body className="font-sans antialiased bg-white text-gray-900 dark:bg-gray-950 dark:text-gray-100 transition-colors duration-300">
        {/* Skip to main content for accessibility */}
        <a
          href="#main-content"
          className="sr-only focus:not-sr-only focus:absolute focus:top-4 focus:left-4 bg-primary-600 text-white px-4 py-2 rounded-lg z-50"
        >
          Aller au contenu principal
        </a>
        
        {/* Header avec gestion de th√®me */}
        <Header />
        
        {/* Contenu principal - le th√®me s'applique ICI AUSSI */}
        <div id="main-content" className="pt-20 min-h-screen bg-white dark:bg-gray-950">
          {children}
        </div>
        
        {/* Footer avec th√®me */}
        <footer className="bg-gray-50 dark:bg-gray-900 border-t border-gray-200 dark:border-gray-800">
          <div className="container mx-auto px-4 py-8">
            <p className="text-center text-gray-600 dark:text-gray-400">
              ¬© 2024 VIBECRO. Tous droits r√©serv√©s.
            </p>
          </div>
        </footer>
      </body>
    </html>
  );
}